import Vue from 'vue'
import Router from 'vue-router'

function load (component) {
  // '@' is aliased to src/components
  return () => import(`@/${component}.vue`)
}
Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', component: load('HelloWorld') },
    { path: '/login', component: load('Login') },
    { path: '/admin/asa', component: load('AdminASA') },
    { path: '/admin/asa/detail', component: load('AdminASADetail') },
    { path: '/admin/asa/add', component: load('AdminASAAdd') },
    { path: '/sms', component: load('SMS') },
    { path: '/pay', component: load('Pay') },
    { path: '/pay/refresh', component: load('PayRefresh') },
    { path: '/summercamp', component: load('SummerCamp') }
  ]
})



